<template>
  <div :class="'root' + (!isMDEditor ? ' midium' : '')">
    <div class="header">
      <div class="left">
        <input placeholder="..." class="title-input" v-model="title" />
      </div>
      <div class="right">
        <div class="session">
          <el-button
            type="primary"
            v-text="''"
            round
            @click="handlePublish"
          ></el-button>
        </div>
        <div class="session">
          <span class="iconfont icon-change" @click="handleChangeEditor"></span>
        </div>
        <div class="session userImage">
          <el-image
            :src="user.userImage"
            title=""
            @click="handleGotoPerson"
          ></el-image>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="main">
      <MavonEditor v-if="isMDEditor" :content="content" ref="md" />
      <YkEditor v-else :getContent="getContent" :content="content" ref="yk" />
    </div>
  </div>
</template>

<script>
import YkEditor from "@/components/editor/yk-editor";
import MavonEditor from "@/components/editor/mavon-editor";
// import Upload from '@/components/upload/singleUpload';
export default {
  name: "editorPassage",
  components: { YkEditor, MavonEditor },
  data() {
    return {
      isMDEditor: true,
      content: "",
      title: "",
    };
  },
  computed: {
    user() {
      return JSON.parse(sessionStorage.getItem("userInfo")) || false;
    },
  },
  mounted() {
    //     ## promise
    // ```javascript promise
    // let a = async function() {
    //   return await new Promise(() => {
    //     resolve()
    //   )
    // }
  },
  methods: {
    // 
    handleGotoPerson() {
      this.$router.push("/user");
    },

    // 
    handleChangeEditor() {
      let con = window.confirm(
        `${this.isMDEditor ? "markdown" : ""}${
          !this.isMDEditor ? "markdown" : ""
        }~~`
      );
      if (con) {
        this.isMDEditor = !this.isMDEditor;
      }
    },

    //
    handlePublish() {
      this.getContent();
      if (!this.isLegal()) return;

      this.$message.success("...");
      this.$nprogress.start();
      setTimeout(() => {
        this.$nprogress.done();
        this.$router.push({
          name: "essays",
          params: {
            title: this.title,
            content: this.content,
            publishTime: new Date().toLocaleDateString()
          },
        });
      }, 1000);
    },

    // 
    isLegal() {
      if (!this.title || this.content.length < 10) {
        this.$notify({
          title: "",
          message: !this.title
            ? ""
            : "",
          type: "warning",
          offset: 120,
          duration: 2000,
        });
        return false;
      }
      return true;
    },

    //
    getContent() {
      if (this.isMDEditor) this.content = this.$refs.md.editorContent;
      else this.content = this.$refs.yk.editorContent;
    },
  },
};
</script>

<style scoped lang='scss'>
.root {
  height: 100%;

  .header {
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .title-input {
      height: 4rem;
      border: none;
      outline: none;
      font-size: 1.8rem;
      background-color: #f4f5f5;
      padding-left: 2rem;
    }

    .right {
      display: flex;
      align-items: center;

      .session {
        margin: 0 1rem;
        cursor: pointer;
      }

      .userImage {
        width: 3rem;
        overflow: hidden;
        border-radius: 50%;

        .el-image {
          transition: 0.4s;
          border-radius: 50%;
        }
      }

      .userImage:hover .el-image {
        transform: scale(130%);
      }
    }
  }
}

.midium {
  width: 50%;
  margin: 0 auto;
}
</style>